import React from "react";
import styled from "styled-components";

const Readme = () => (
  <Wrap>
    <H3>:</H3>
    <Ul>
      <Li>1.    .</Li>
      <Li>2.    .</Li>
      <Li>3.  ,   ID.</Li>
      <Li> :  redux,    .</Li>
    </Ul>
    <br />
    <H3>  :</H3>
    <Ul>
      <Li>
        1.   connect  redux   , 
          props.
      </Li>
      <Li>
        2.        ID  
          redux.
      </Li>
    </Ul>
    <br />
    <H3> :</H3>
    <P>   ,  :</P>
    <Ul>
      <Li>1.     redux</Li>
      <Li>
        2.      redux,      
           ..      redux 
         
      </Li>
    </Ul>
    <br />
    <H3> :</H3>
    <Ul>
      <Li>
        1.    ,  
        <A
          href="https://github.com/react-tools/react-table"
          target="_blank"
          rel="noopener noreferrer"
        >
          react-table
        </A>
            .
      </Li>
      <Li>
        2.      .
         PureCopmonent    , 
         (  ,  ..)
      </Li>
    </Ul>
  </Wrap>
);

export default Readme;

const Wrap = styled.div``;

const H3 = styled.h3`
  font-size: 4.6rem;
`;

const Ul = styled.ul``;

const Li = styled.li`
  font-size: 2.2rem;
`;

const P = styled.p`
  font-size: 2rem;
`;

const A = styled.a`
  font-size: 2.2rem;
  font-weight: 700;
  margin: 0 10px;
  text-decoration: underline;
`;



